function ChartConfigService($q, $resource, API) {
      var factory = {}
        factory.lineChartConfig ={refreshDataOnly: false}
        factory.lineChartOptions = {
            chart: {
                 type: 'lineChart',
                height: '400',
                legend: {
       				 vers: 'classic',
       				 //padding: { top: 20 },
       				 maxKeyLength:400,
       				margin : {
                    top: 20,
                    right: 50,
                    bottom: 50,
                    left: 50
                }
     			 },
      			legendPosition: 'top',
                margin : {
                    top: 50,
                    right: 50,
                    bottom: 100,
                    left: 80
                },
                x: function(d){
                    return d[0];
                },
              //  y: function(d){ return d[1]/100; },
                y: function(d){
                    return d[1];
                },
             //   average: function(d) { return d.mean/100; },

                color: d3.scale.category10().range(),
                duration: 300,
                useInteractiveGuideline: true,
                clipVoronoi: false,
                showYAxis:true,
                xAxis: {
                    tickSize:'10',
                    axisLabel: 'Per Hour',
                     rotateLabels: '-90',
                    tickFormat: function(d) {
                     //   return d3.time.format('%d %B %y')(new Date(d))
                        return d3.time.format('%I %p')(new Date(d))
                    },

                    showMaxMin: true,
                    staggerLabels: false,
                    axisLabelDistance: 200
                },
                x2Axis: {
                    axisLabel: 'Date',
                    tickFormat: function(d) {
                        //   return d3.time.format('%d %B %y')(new Date(d))
                        return d3.time.format('%I %p')(new Date(d))
                    },

                    showMaxMin: true,
                    staggerLabels: false
                },

                yAxis: {
                    axisLabel: 'Trips',
                    tickFormat: function(d){
                        return d;
                       // return d3.format(',.1%')(d);
                    },
                    showMaxMin: true,
                    axisLabelDistance: 200
                }
            }
        };

    return factory
}

(function () {
    'use strict';

    angular
        .module('tuktukV2Dahboard')
        .run(runBlock);

    /** @ngInject */
    function runBlock($log, UserService, StaticDataService, $http, toastr, $state, $rootScope, LoginService, $templateCache) {
        if (UserService.getUser() != null) {
            $http.defaults.headers.common.Authorization = 'Basic ' + UserService.getUser().token;
        }
        $log.debug('runBlock end');
        $rootScope.city = StaticDataService.cities[0].value
        $rootScope.vehicleType = StaticDataService.vehicleTypes[0].value
        /* $rootScope.$on('$viewContentLoaded', function() {
         $templateCache.removeAll();
         });*/
        $rootScope.logout = function () {
            LoginService.logout({}, function (response) {
                    toastr.error("You have logged out successfully.");
                },
                function (error) {
                    toastr.error(error);
                })
            UserService.clearUser()
            $state.go("login")
            $rootScope.isAuthenticated = false;
        }

        $rootScope.$on('$stateChangeStart', function (e, toState, toParams, fromState, fromParams) {
            console.log("to " + toState.name + " $state.current.name " + $state.current.name)
            if (UserService.getUser() != null && (toState.name == "login")) {
                $rootScope.isAuthenticated = true;
                $state.go("home.performance")
                e.preventDefault();
                //   return

            }
        });
    }

})();

(function () {
    'use strict';

    angular
        .module('tuktukV2Dahboard')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {

        $urlRouterProvider.otherwise('/login');
        $stateProvider
            .state('login', {
                url: '/login',
                templateUrl: 'app/login/login.html',
                controller: 'LoginController',
                controllerAs: 'vm'
            })
            .state('home', {
                url: '/home',
                templateUrl: 'app/main/main.html',
                controller: 'MainController',
                controllerAs: 'main',
                abstract: true
            })
            .state('home.performance', {
                url: '/performance/:city/:vehicleType',
                templateUrl: 'app/performance/performance.html',
                controller: 'PerformanceController',
                controllerAs: 'vm'
            })
            .state('home.maps', {
                url: '/maps/:city/:vehicleType',
                templateUrl: 'app/maps/maps.html',
                controller: 'MapsController',
                controllerAs: 'vm'
            })
            .state('home.drivers', {
                url: '/drivers',
                templateUrl: 'app/drivers/drivers.html',
                controller: 'DriversController',
                controllerAs: 'drivers'
            });


    }

})();

